@font-face {
  font-family: "Poppins";
  src: url("../../front/assets/fonts/Poppins-Medium.ttf") format("truetype");
}

body {
  margin: 0;
  background-color: #252954;
}

.hidden {
  display: none;
}

.mt-2 {
  margin-top: 0.5em !important;
}
.landingpage {
  margin: 0 auto;
  padding: 0 80px;
  position: relative;
  overflow-y: auto;
  overflow-x: hidden;
  background-color: #1f1d2b;
  &::before {
    content: "";
    position: absolute;
    width: 123px;
    height: 123px;
    left: 60%;
    top: 138px;
    filter: blur(90px);
    background-color: #fb37ff;
  }
  &::after {
    content: "";
    position: absolute;
    width: 123px;
    height: 123px;
    left: 80%;
    top: 550px;
    background-color: #18b2de;
    filter: blur(80px);
  }
  .navbar {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 20px 0;
    width: 100%;
  }
  .box {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 80px;
  }
  .auction {
    margin-top: 100px;
  }
  .discover {
    display: flex;
    flex-direction: column;
  }
  .footer {
    margin: 50px 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .footer2 {
    display: flex;
    justify-content: space-between;
    padding-bottom: 30px;
    border-top: 1px solid #f2f2f2;
  }
}

.navbar {
  .hamburgerlogowrap {
    display: flex;
    align-items: center;
  }
  .hamburger {
    display: none;
    color: #d7d7d7;
    background-color: #1f1d2b;
    border: none;
    margin-right: 10px;
  }
  .createbtn {
    cursor: pointer;
    background-color: transparent;
    border: none;
    width: 126px;
    height: 45px;
    color: #bcbcbc;
    font-family: "Poppins";
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    &.selectedbtn {
      border: 1px solid #d7d7d7;
      border-radius: 10px;
    }
  }
  .navlogo {
    height: 100%;
    background: linear-gradient(93.51deg, #9b51e0 2.84%, #3081ed 99.18%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    font-family: "Poppins";
    font-style: normal;
    font-weight: 700;
    font-size: 32px;
  }
  .navlink {
    font-family: "Poppins";
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    color: #bcbcbc;
    &.selectedlink {
      background: linear-gradient(93.51deg, #9b51e0 2.84%, #3081ed 99.18%);
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
      border-bottom: 1.5px solid #9b51e0;
    }
    &:not(:last-child) {
      margin-right: 32px;
    }
  }
}
.box {
  .infobox {
    animation-name: leftslide;
    animation-duration: 0.8s;
    max-height: 500px;
    max-width: 55%;
    overflow: hidden;
    &-boldtext {
      margin: 0;
      font-family: Poppins;
      color: #ffffff;
      font-size: 65px;
      font-weight: 600;
      line-height: 60px;
      letter-spacing: -2px;
      text-align: left;
    }
    &-slimtext {
      margin: 24px 0;
      font-family: Poppins;
      color: #ffffff;
      font-size: 16px;
      font-weight: 400;
      line-height: 18px;
      letter-spacing: 0.5px;
      text-align: left;
    }
    &-btnwrapper {
      display: flex;
    }
    &-explorebtn {
      cursor: pointer;
      display: flex;
      justify-content: center;
      align-items: center;
      background-color: #1f1d2b;
      padding: 8px 38px;
      border-radius: 16px;
      letter-spacing: 1px;
      font-weight: 600;
      font-size: 13px;
      letter-spacing: -0.5px;
      color: #ffffff;
    }
    &-createbtn {
      cursor: pointer;
      margin-left: 20px;
      display: flex;
      justify-content: center;
      align-items: center;
      background-color: transparent;
      padding: 8px 38px;
      border: 1px solid #d7d7d7;
      border-radius: 16px;

      font-family: "Poppins";
      font-style: normal;
      font-weight: 600;
      font-size: 13px;
      color: #ffffff;
    }
    .selected {
      border: none;
      background: linear-gradient(
        103.91deg,
        #9b51e0 21.01%,
        rgba(48, 129, 237, 0.8) 100%
      );
    }
  }
  .display {
    animation-name: rightslide;
    animation-duration: 0.8s;
    padding: 14px;
    border: 1px solid;
    background: linear-gradient(
      169.44deg,
      rgba(58, 129, 191, 0.08) 1.85%,
      rgba(65, 48, 90, 0.08) 98.72%
    );
    border-radius: 35px;
    max-height: 450px;
    max-width: 50%;
    overflow: hidden;
    &-nft {
      width: 340px;
      height: 340px;
      border-radius: 20px;
    }

    .infowrapper {
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin-top: 10px;
    }

    .info {
      display: flex;
      align-items: center;
      font-family: "Poppins";
      font-style: normal;
      color: #ffffff;
      font-weight: 600;
      font-size: 12px;
      p {
        margin: 0;
      }
      &-img {
        object-fit: cover;
        flex-shrink: 0;
        width: 30px;
        height: 30px;
        border-radius: 50%;
        margin-right: 10px;
      }
    }
    .info2 {
      display: flex;
      flex-direction: column;
      align-items: flex-end;
      font-family: "Poppins";
      color: #ffffff;
      font-style: normal;
      font-weight: 600;
      font-size: 12px;
      p {
        margin: 0;
      }
      .iconwrapper {
        display: flex;
        align-items: center;
        svg {
          margin-right: 5px;
        }
      }
    }
  }
}
.auction {
  .title {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    margin-bottom: 50px;
    .titlebold {
      margin: 0;
      font-family: "Poppins";
      font-style: normal;
      color: #ffffff;
      font-weight: 500;
      font-size: 48px;
      line-height: 72px;
    }
    .titleslim {
      margin: 0;
      font-family: "Poppins";
      font-style: normal;
      color: #d7d7d7;
      font-weight: 600;
      font-size: 18px;
      line-height: 36px;
      letter-spacing: -1px;
    }
  }
  .nft {
    display: grid;
    grid-template-columns: auto auto auto auto auto;
    grid-row-gap: 20px;
    grid-column-gap: 20px;
    justify-content: space-between;
  }
}
.started {
  margin-top: 100px;
  display: flex;
  flex-direction: column;
  &-boldtext {
    text-align: center;
    margin: 0;
    align-self: center;
    font-family: "Poppins";
    font-style: normal;
    color: #ffffff;
    font-weight: 500;
    font-size: 40px;
    line-height: 72px;
  }
  &-slimtext {
    margin: 0;
    align-self: center;
    font-family: "Poppins";
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 36px;
    letter-spacing: 0.5px;
    color: #e2e2e2;
  }
  &-items {
    padding: 60px 80px;
    position: relative;
    display: grid;
    grid-template-columns: auto auto auto auto auto;
    grid-column-gap: 20px;
    grid-row-gap: 20px;
    justify-content: space-between;
    &:before {
      content: "";
      position: absolute;
      width: 100%;
      height: 100px;
      top: 80px;
      filter: blur(80px);
      background: linear-gradient(93.51deg, #9b51e0 2.84%, #3081ed 99.18%);
    }
    .itemwrapper {
      p {
        width: 100px;
        margin: 0;
        margin-top: 10px;
        text-align: center;
        font-family: "Poppins";
        font-style: normal;
        font-weight: 400;
        font-size: 12px;
        color: #ffffff;
      }
    }
    &-item {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 100px;
      height: 100px;
      border-radius: 18px;
      background-color: red;
      background: rgba(255, 255, 255, 0.095);
      box-shadow: inset 2.01px -2.01px 20px rgba(214, 214, 214, 0.17),
        inset -3.01333px 3.01333px 3.01333px rgba(255, 255, 255, 0.39);
      backdrop-filter: blur(74.4293px);
    }
  }
}
.discover {
  &-title {
    display: flex;
    align-items: center;
    justify-content: space-between;
    p {
      font-family: "Poppins";
      font-style: normal;
      font-weight: 500;
      font-size: 32px;
      letter-spacing: -1px;
      color: #ffffff;
    }
  }
  &-items {
    display: grid;
    grid-template-columns: auto auto auto auto auto;
    grid-row-gap: 20px;
    grid-column-gap: 20px;
    justify-content: space-between;
  }
  &-loadbtn {
    cursor: pointer;
    margin-top: 20px;
    align-self: center;
    width: 161px;
    height: 40px;
    border: 1px solid #d7d7d7;
    border-radius: 10px;
    background-color: transparent;
    font-family: "Poppins";
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    letter-spacing: 1.5px;
    color: #bcbcbc;
  }
}
.footer {
  &-main {
    max-width: 40%;
    font-family: "Poppins";
    font-style: normal;
    font-weight: 500;
    font-size: 30px;
    line-height: 40px;
    color: #ffffff;
  }
  &-navigate {
    display: flex;
    .nav {
      margin-left: 60px;
      h5 {
        margin: 0;
        font-family: "Poppins";
        font-style: normal;
        font-weight: 400;
        font-size: 20px;
        line-height: 30px;
        color: #ffffff;
      }
      p {
        font-family: "Poppins";
        font-style: normal;
        font-weight: 400;
        font-size: 12px;
        line-height: 18px;
        letter-spacing: 0.03em;
        color: #d7d7d7;
      }
    }
  }
}
.footer2 {
  p {
    font-family: "Poppins";
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 36px;
    color: #828282;
  }
}

.item {
  display: flex;
  flex-direction: column;
  justify-self: center;
  background-color: #2d3159;
  width: 150px;
  min-height: 220px;
  background-size: cover;
  filter: blur(8px);
  -webkit-filter: blur(0.2px);
  background-image: url("https://images.unsplash.com/photo-1620641788421-7a1c342ea42e?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=1074&q=80");
  padding: 1em;
  border-radius: 1em;
  &-img {
    margin: auto;
    width: 100px;
    height: 100px;
    transition: 0.3s;
    cursor: pointer;
    &:hover {
      transform: scale(1.05);
    }
  }
  &-title {
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-bottom: 1px solid white;

    font-family: "Poppins";
    font-style: normal;
    font-weight: 600;
    font-size: 12px;
    line-height: 24px;
    color: #ffffff;
  }
  &-date {
    font-family: "Poppins";
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 24px;
    color: #bcbcbc;
  }
}
.filters {
  display: flex;
  align-items: center;
  .filter {
    display: flex;
    align-items: center;
    margin-right: 30px;
    font-family: "Poppins";
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    color: #ffffff;
    svg {
      margin-right: 5px;
      margin-left: 5px;
    }
  }
  .filterbtn {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100px;
    height: 30px;
    background: linear-gradient(93.51deg, #9b51e0 2.84%, #3081ed 99.18%);
    border: none;
    border-radius: 10px;
    font-family: "Poppins";
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    color: #ffffff;
    svg {
      margin-right: 5px;
    }
  }
}

@media screen and (max-width: 1500px) {
  .landingpage {
    max-width: 1200px;
  }
}

@media screen and (min-width: 1500px) {
  .itemwrapper {
    .started-items-item {
      margin: auto;
    }
    p {
      width: 200px !important;
      margin: auto;
      text-align: "center";
    }
  }
}
@media screen and (max-width: 1040px) {
  .box {
    flex-direction: column;
    .infobox {
      max-height: 450px;
      max-width: none;
      margin-bottom: 50px;
      &-boldtext {
        text-align: center !important;
      }
      &-slimtext {
        text-align: center !important;
      }
      &-btnwrapper {
        justify-content: center !important;
      }
    }
    .display {
      display: none;
      max-height: none;
      max-width: none;
    }
  }
  .auction {
    .nft {
      grid-template-columns: auto auto;
      justify-content: space-around;
    }
  }
  .discover-items {
    grid-template-columns: auto auto auto;
    justify-content: space-around;
  }
  .footer {
    flex-direction: column;
    &-main {
      max-width: 80%;
      margin-bottom: 50px;
      text-align: center;
    }
    &-navigate {
      .nav {
        &:first-child {
          margin: 0;
        }
      }
    }
  }
}

@media screen and (max-width: 925px) {
  .navlinkwrap {
    display: none;
  }
  .buttonwrap {
    display: none;
  }
  .hamburger {
    display: flex !important;
    align-items: center;
  }
  .started-slimtext {
    text-align: center;
  }
}

@media screen and (max-width: 825px) {
  .started-items {
    display: grid;
    grid-template-columns: auto auto;
    justify-content: space-evenly;
    padding: 50px 0;
    &:before {
      height: 200px;
      top: 90px;
      filter: blur(120px);
    }
  }

  .discover-items {
    grid-template-columns: auto auto;
    justify-content: space-around;
  }
}

@media screen and (max-width: 700px) {
  .box {
    flex-direction: column;
    .infobox {
      max-height: none;
      max-width: none;
      margin-bottom: 50px;

    }
    .display {
      display: block;
      max-height: none;
      max-width: none;
    }
  }

  .discover-title {
    justify-content: center;
    .filters {
      display: none;
    }
  }
  .auction {
    .title {
      justify-content: center;
      .titleslim {
        display: none;
      }
    }
  }
}
.discover-items {
  button {
    background: transparent;
    box-shadow: 0px 0px 0px transparent;
    border: 0px solid transparent;
    text-shadow: 0px 0px 0px transparent;
  }
}
@media screen and (max-width: 600px) {
  .landingpage {
    padding: 0 20px;
  }
  .display-nft {
    width: 200px !important;
    height: 200px !important;
  }
  .box {
    .infobox {
      &-boldtext {
        text-align: center;
        font-size: 45px;
        line-height: 50px;
      }
      &-slimtext {
        text-align: center;
        font-size: 12px;
        line-height: 16px;
      }
      &-btnwrapper {
        justify-content: center;
      }
    }
  }
  .discover-items {
    grid-template-columns: auto;
    justify-content: space-around;
  }
  .auction {
    .nft {
      grid-template-columns: auto;
      justify-content: space-around;
    }
  }
  .footer-main {
    font-size: 20px;
    line-height: 30px;
    margin-bottom: 50px;
    text-align: center;
  }
  .footer-navigate {
    justify-content: space-between;
    width: 100%;
    .nav {
      margin-left: 20px;
      h5 {
        font-size: 15px;
        line-height: 30px;
      }
      p {
        font-size: 10px;
        line-height: 18px;
      }
    }
  }
}

@media screen and (max-width: 480px) {
  .landingpage {
    .box {
      .infobox {
        &-boldtext {
          line-height: 1.4em;
          text-align: center;
        }
        &-slimtext {
          text-align: center;
        }
        &-btnwrapper {
          justify-content: center;
        }
      }
    }
  }
}

@keyframes leftslide {
  from {
    transform: translateX(-700px);
  }
  to {
    transform: translateX(0px);
  }
}

@keyframes rightslide {
  from {
    transform: translateX(550px);
  }
  to {
    transform: translateX(0px);
  }
}

.mb-4 {
  margin-bottom: 4em !important;
}

.react-icon {
  width: 20px;
  height: 20px;
  cursor: pointer;
  &:nth-child(1) {
    margin-right: 25px;
  }
}

.discover-items {
  button {
    cursor: pointer;
  }
}