.sellix-iframe-loader {
  animation-name: spin;
  animation-duration: 2s;
  animation-iteration-count: 5;
  animation-timing-function: ease-in-out;
  animation-delay: 0s;
  width: 35px !important;
  height: 35px !important;
}
@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  50% {
    transform: rotate(360deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
